@import "../../../styles/utils";

.title{
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 2rem;
  font-weight: 400;
}
.list-wrapper {
  width: 80%;
  margin: 0 auto;

  padding-top: 3rem;
  padding-bottom: 3rem;
  padding-left: 1rem;
  padding-right: 1rem;

  @include media("<wide"){
    width: 768px;
  }
  @include media("<large"){
    width: 512px;
  }
  @include media("<medium"){
    width: 100%;
  }
}